import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators, FormControl} from '@angular/forms';
import {PageTitleService} from '../../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../../core/route-animation/route.animation";
import {Service} from "../metier/service.metier";
import {ServiceService} from "../service/service.service";
import {BaseComponent} from "../../../core/component/base-component";
import {Params, ActivatedRoute} from "@angular/router";

@Component({
  selector: 'ms-service-edit',
  templateUrl: './service.edit.component.html',
  styleUrls: ['./service.edit.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class ServiceEditComponent extends BaseComponent implements OnInit {
  private service:Service;
  public serviceForm:FormGroup;


  constructor(private fb:FormBuilder,
              private route:ActivatedRoute,
              private pageTitleService:PageTitleService,
              private serviceService:ServiceService) {
    super();
  }

  ngOnInit() {
    this.loadService();
    this.pageTitleService.setTitle("MODIFIER UN SERVICE");
    this.serviceForm = this.fb.group({
      id: [Validators.required],
      libelle: ['', Validators.required],

    });
  }


  updateService(serviceForm:any) {
    this.service = new Service(serviceForm);
    this.serviceService.saveService(this.service)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.router.navigate(['/admin/service/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  loadService() {
    this.route.params.switchMap((params:Params) => this.serviceService.getService(+params['id']))
      .subscribe(service => {
          this.service = service;
          this.serviceForm.patchValue(this.service);
        }, error => this.showError(error.status, error.json().message)
      );
  }

  deleteService(serviceForm:any) {
    if (!confirm("Etes-vous de vouloir supprimer ces enregistrements !")) {
      return;
    }
    this.service = new Service(serviceForm);
    this.serviceService.deleteService(this.service)
      .subscribe(() => {
        this.showInfo("Suppression effectué avec succès");
        this.router.navigate(['/admin/service/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
    ;
  }


}






