"use strict";

import {Component, ViewChild, OnInit, ViewEncapsulation} from '@angular/core';
import {PageTitleService} from '../../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../../core/route-animation/route.animation";
import {Service} from "../metier/service.metier";
import {FormGroup, FormBuilder} from "@angular/forms";
import {BaseComponent} from "../../../core/component/base-component";
import {ServiceService} from "../service/service.service";
import 'rxjs/Rx' ;
import 'rxjs/add/operator/toPromise';
import {saveAs} from 'file-saver/FileSaver';

@Component({
  selector: 'ms-service-list',
  templateUrl: './service.list.component.html',
  styleUrls: ['./service.list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]

})
export class ServiceListComponent extends BaseComponent implements OnInit {

  public serviceForm: FormGroup;
  private service: Service;
  private serviceList: Array<Service> = [];
  private file: Blob;

  constructor(private fb: FormBuilder,
              private serviceService: ServiceService,
              private pageTitleService: PageTitleService) {
    super();
    this.createForm();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("LISTE DES SERVICES");
    this.loadServiceList();

  }

  createForm() {
    this.serviceForm = this.fb.group({
      libelle: '',
    });
  }


  findServiceList(serviceForm: any) {

    this.service = new Service(serviceForm);
    this.serviceService.findServiceListByCriteria(this.service)
      .subscribe(serviceList => {
        this.serviceList = serviceList;
        this.getPdfFile();

      }, error => {

        this.showError(error.status, error.json().message);
      });
  }

  private loadServiceList() {

    this.serviceService.getServiceList()
      .subscribe(serviceList => {
        this.serviceList = serviceList;
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  private getPdfFile() {
    this.serviceService.downloadPdf()
      .subscribe(file => {
          this.file = file;
          this.downloadFile(this.file);
        }, error => this.showError(error.status, error.json().message)
      );
  }

}






